.notfound-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #f0f0f0;
    padding: 20px;
    text-align: center;
}

.notfound-icon {
    font-size: 4rem;
    color: #6200ea;
    margin-bottom: 20px;
}

.notfound-title {
    font-size: 2rem;
    color: #6200ea;
    margin-bottom: 20px;
}

.notfound-message {
    font-size: 1.2rem;
    color: #333;
}

a {
    color: #6200ea;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}



