/* BrieflyLink.css */
.outer-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: #f0f0f0;
    padding: 10px; /* Reduzido para ajustar o espaço ao redor */
}

.header-container {
    text-align: center;
    margin-bottom: 10px; /* Reduzido para diminuir o espaço entre o header e o restante do conteúdo */
}

.home-title {
    font-size: 4rem;
    color: #6200ea;
    margin-bottom: 10px;
}

.home-description {
    font-size: 1.5rem;
    color: #333;
}

.inner-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    padding: 20px;
    padding-bottom: 30px; /* Reduzido para criar mais espaço na parte inferior */
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    width: 90%;
    max-width: 600px;
    text-align: center;
    margin-top: 10px; /* Adicionado para criar mais espaço acima */
}

.label {
    font-size: 1.5rem;
    margin-bottom: 18px;
    color: #333;
    font-weight: bold;
}

.input {
    width: 80%;
    padding: 12px;
    margin-bottom: 18px;
    border: 2px solid #6200ea;
    border-radius: 5px;
    font-size: 1rem;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.button {
    padding: 12px 24px;
    font-size: 1.2rem;
    color: #fff;
    background-color: #6200ea;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
}

.button:hover {
    background-color: #3700b3;
    transform: scale(1.05);
}

.error {
    color: red;
    margin-top: 20px;
    font-weight: bold;
}

.response-container {
    margin-top: 20px;
    padding: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #f9f9f9;
    width: 100%;
    max-width: 600px;
    text-align: center;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.response-container h3 {
    margin-top: 0;
    color: #6200ea;
    font-weight: bold;
}

.response-container p {
    margin: 10px 0;
    color: #333;
}

.response-container a {
    color: #6200ea;
    text-decoration: none;
    font-weight: bold;
}

.response-container a:hover {
    text-decoration: underline;
}

.qr-code {
    display: block;
    margin: 10px auto;
    max-width: 100%;
    height: auto;
}

.terms-of-service {
    margin-top: 25px; /* Reduzido para diminuir o espaço acima */
}

.terms-of-service a {
    color: #6200ea;
    text-decoration: none;
    font-weight: bold;
    font-size: 1rem;
}

.terms-of-service a:hover {
    text-decoration: underline;
}

/* Estilos para dispositivos móveis */
@media (max-width: 600px) {
    .home-title {
        font-size: 2.5rem; /* Ajustado para dispositivos móveis */
    }

    .home-description {
        font-size: 1rem; /* Ajustado para dispositivos móveis */
    }

    .label {
        font-size: 1rem; /* Ajustado para dispositivos móveis */
    }

    .input {
        width: 80%; /* Ajustado para preencher a largura da tela em dispositivos móveis */
    }

    .button {
        padding: 10px 20px; /* Ajustado para dispositivos móveis */
        font-size: 1rem; /* Ajustado para dispositivos móveis */
    }

    .response-container {
        padding: 15px; /* Ajustado para dispositivos móveis */
    }

    .response-container p {
        font-size: 0.875rem; /* Ajustado para dispositivos móveis */
    }

    .terms-of-service a {
        font-size: 0.875rem; /* Ajustado para dispositivos móveis */
    }
}
